import axios from 'axios'
import {
  buildUrlJwt,
} from '../config'
import { destResponse } from '../utils/main'

export const fetchProjects = parameters => new Promise(resolve =>
  axios
    .get(buildUrlJwt('projects', parameters))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const fetchProject = id => new Promise(resolve =>
  axios
    .get(buildUrlJwt(`projects/${id}`))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const createProject = (parameters, getParams = null) => new Promise(
  resolve =>
    axios
      .post(buildUrlJwt('projects', getParams), parameters)
      .then(res => resolve(destResponse(res)))
      .catch(err => resolve(destResponse(err, false))),
)

export const updateProject = (id, parameters) => new Promise(resolve =>
  axios
    .put(buildUrlJwt(`projects/${id}`), parameters)
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const deleteProject = id => new Promise(resolve =>
  axios
    .delete(buildUrlJwt(`projects/${id}`))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

// Tasks for project
export const fetchTasks = (project, parameters) => new Promise(resolve =>
  axios
    .get(buildUrlJwt(`projects/${project}/tasks`, parameters))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const createTask = (project, task) => new Promise(resolve =>
  axios
    .post(buildUrlJwt(`projects/${project}/tasks`), task)
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const updateTask = (project, task, parameters) => new Promise(resolve =>
  axios
    .put(buildUrlJwt(`projects/${project}/tasks/${task}`), parameters)
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const deleteTask = (project, task) => new Promise(resolve =>
  axios
    .delete(buildUrlJwt(`projects/${project}/tasks/${task}`))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const updateTasksSort = (project, parameters) => new Promise(resolve =>
  axios
    .put(buildUrlJwt(`sort/projects/${project}/tasks`), {data: parameters})
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)


