<template>
    <div>
        <spinner :enable="spinnerEnable"/>

        <div class="row col-md-12 projects"
             v-if="!spinnerEnable">
            <div class="row col-md-10 all-projects">
                <div class="col-md-12 header-projects">
                    <div v-if="!isCreating">
                        <input type="text"
                               class="col-md-10 search-input"
                               placeholder="Поиск..."
                               v-model="search"
                               @keyup.enter="searchByName"
                               @dblclick="init">
                        <button class="btn add-project"
                                @click="isCreating = true">
                            Добавить
                        </button>
                    </div>
                    <div v-else>
                        <input type="text"
                               class="col-md-12 input-new_project-name"
                               placeholder="Название..."
                               v-model="newProject.name"
                               @keyup.enter="store">
                    </div>
                </div>
                <div class="col-md-12 pagination">
                    <div class="left-arrow"
                         v-if="8 <= skip"
                         @click="paginationPrev">
                        <span>&#60;</span>
                    </div>
                    <div class="right-arrow"
                         v-if="0 !== projects.length"
                         @click="paginationNext">
                        <span>&#62;</span>
                    </div>
                </div>
                <div class="row col-md-11 list-projects">
                    <transition name="bounce">
                        <div class="row col-md-12"
                             v-if="!fadeAnimation">
                            <div class="col-sm-3 project"
                                 v-for="project in projects"
                                 :key="project.id">
                                <div class="card">
                                    <div class="card-body">
                                        <div v-if="editable.id !== project.id && project.id !== removable.id">
                                            <h5 class="card-title"
                                                @dblclick="editable = project"
                                                :title="project.name">
                                                {{ project.name | projectLongName }}
                                            </h5>
                                            <div @dblclick="removable = project"
                                                 ref="removeEvent">
                                                <p class="card-text">Количество задач {{ project.tasks_count }}</p>
                                                <p class="card-text">Выполненые задач {{ project.tasks_completed }}</p>
                                                <p class="card-text">
                                                    Осталось задач {{ project.tasks_count - project.tasks_completed }}
                                                </p>
                                            </div>
                                        </div>
                                        <transition name="bounce">
                                            <div class="editable-project"
                                                 v-if="editable.id === project.id">
                                                <input type="text"
                                                       v-model="editable.name"
                                                       :title="'Название'">
                                                <input type="text"
                                                       v-model="editable.sort"
                                                       :title="'Позиция'">
                                                <div class="row col-md-12">
                                                    <button class="col-md-8 btn"
                                                            @click="update">
                                                        Обновить
                                                    </button>
                                                    <div class="col-md-1 back"
                                                         @click="editable = {}">
                                                        <span>x</span>
                                                    </div>
                                                </div>
                                            </div>
                                        </transition>
                                        <transition name="fade">
                                            <div v-if="removable.id === project.id"
                                                 @dblclick="removable = {id: 0}">
                                                <button class="btn remove-project"
                                                        @click="remove">
                                                    Удалить?
                                                </button>
                                            </div>
                                        </transition>
                                    </div>
                                </div>
                                <div class="open-project"
                                     @click="$router.push({name: 'project-page', params: {id: project.id}})">
                                </div>
                            </div>
                        </div>
                    </transition>
                </div>
            </div>
            <filters class="col-md-2 filters"
                     v-on:execute="executeFilter"/>
        </div>


    </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import {
    fetchProjects,
    createProject,
    updateProject,
    deleteProject
  } from '../api/project'
  import notify from '../services/notify'
  import Spinner from './Spinner'
  import ProjectFilters from './ProjectFilters'

  const take = 8

  export default {
    name: 'Projects',
    components: {
      Spinner,
      filters: ProjectFilters
    },
    data: () => ({
      skip: 0,
      spinnerEnable: false,
      isCreating: false,
      search: '',
      newProject: {name: ''},
      editable: {id: 0, name: '', sort: 0},
      removable: {id: 0},
      fadeAnimation: false,
      activeFilters: '&order_by_position=1&'
    }),
    async mounted () {
      await this.init()
    },
    computed: {
      ...mapGetters(['projects', 'userIsGuest']),
      myProjects: {
        get () {
          return this.projects
        },
        set (projects) {
          this.setProjects(projects)
        }
      }
    },
    methods: {
      ...mapActions({
        addProject: 'ADD_PROJECT',
        setProjects: 'SET_PROJECTS',
        logout: 'LOGOUT'
      }),
      async init () {
        if (!this.userIsGuest) {
          this.search = ''
          this.spinnerEnable = true
          this.skip = 0
          this.activeFilters = '&order_by_position=1&'

          const {success, result} = await this.loadProjects()

          if (!success) {
            if (undefined === result.response) {
              notify.error({text: 'Произошла ошибка, попробуйте позже'})
            }

            if (401 === result.response.status) {
              notify.warning({text: 'Вы должны авторизоваться'})

              this.logout()

              return this.$router.push('/')
            }
          }

          await this.setProjects(result)

          this.spinnerEnable = false
        }
      },
      async executeFilter (parameters) {
        this.fadeAnimation = true
        this.skip = 0
        this.activeFilters = parameters

        const {success, result} = await this.loadProjects(`&search_by=${this.search}`)

        if (success) {
          await this.setProjects(result)
        } else {
          notify.error({text: 'Произошла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
      async searchByName () {
        if (0 === this.search.length) {
          return
        }

        this.fadeAnimation = true
        this.skip = 0

        const {success, result} = await this.loadProjects(`&search_by=${this.search}`)

        if (success) {
          await this.setProjects(result)
        } else {
          notify.error({text: 'Произошла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
      store () {
        if (2 >= this.newProject.name.length) {
          notify.warning({text: 'Минимальное название должно состоять из 3х символов\''})

          return
        }

        createProject({name: this.newProject.name})
          .then(({success, result}) => {
              if (success) {
                this.addProject(result.data.data)

                this.newProject.name = ''

                notify.success({text: 'Вы успешно добавили новый проект'})
              } else {
                notify.error({text: 'Произошла ошибка, попробуйте позже'})
              }
            }
          )

        this.isCreating = false
      },
      update () {
        if (0 === this.editable.name.length) {
          notify.warning({text: 'Вы забыли ввести название'})

          return
        }

        updateProject(this.editable.id, {
          name: this.editable.name,
          sort: this.editable.sort
        }).then(({success, _}) => {
          if (success) {
            this.editable = {id: 0, name: '', sort: 0}

            this.init()

            notify.success({text: 'Вы успешно обновили проект'})
          } else {
            notify.error({text: 'Произошла ошибка, попробуйте позже'})
          }
        })
      },
      remove () {
        deleteProject(this.removable.id)
          .then(({success, _}) => {
            if (success) {
              this.removable = {id: 0}

              notify.success({text: 'Вы успешно удалили проект'})

              this.init()
            } else {
              notify.error({text: 'Произошла ошибка, попробуйте позже'})
            }
          })
      },
      async loadProjects (getParams = null) {
        const {success, result} =
          null === getParams
            ? await fetchProjects(`${this.activeFilters}&skip=${this.skip}&take=${take}`)
            : await fetchProjects(`${getParams}&${this.activeFilters}&skip=${this.skip}&take=${take}`)

        if (success) {
          return {success, result: result.data.data}
        }

        return {success, result}
      },
      async paginationPrev () {
        if (0 === this.skip) {
          return
        }

        this.fadeAnimation = true
        this.skip -= take

        const {success, result} = await this.loadProjects(`&search_by=${this.search}`)

        if (success) {
          if (0 !== result.length) {
            this.setProjects(result)
          } else {
            this.skip += take

            notify.info({text: 'Проектов нету :('})
          }
        } else {
          notify.error({text: 'Возникла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
      async paginationNext () {
        this.fadeAnimation = true
        this.skip += take

        const {success, result} = await this.loadProjects(`&search_by=${this.search}`)

        if (success) {
          if (0 !== result.length) {
            this.setProjects(result)
          } else {
            this.skip -= take

            notify.info({text: 'Проектов нету :('})
          }
        } else {
          notify.error({text: 'Возникла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      }
    }
  }
</script>

<style scoped>
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
    {
        opacity: 0;
    }

    .bounce-enter-active {
        animation: bounce-in 0.5s;
    }

    .bounce-leave-active {
        animation: bounce-in 0.5s reverse;
    }

    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }

    input::-webkit-input-placeholder {
        color: #fff;
    }

    .projects {
        background: #fff;
        margin: 60px 0;
        height: 450px;
    }

    .all-projects {
        height: 100%;
        background: #ec8989;
    }

    .header-projects {
        height: 50px;
        border-bottom: solid 2px #fff;
        margin: 10px auto;
    }

    .header-projects input {
        height: 40px;
        margin-left: 10px;
        outline: none;
        border: none;
        background: none;
        color: #fff;
    }

    .header-projects button {
        margin-left: 55px;
        background: none;
        border: solid 1px #fff;
        color: #fff;
    }

    .header-projects button:hover {
        background: #d88080;
        border: solid 1px #d88080;
    }

    .list-projects {
        margin: 0 55px;
    }

    .list-projects .project {
        margin: -10px 0 50px 0;
        height: 130px;
        font-size: 13px;
    }

    .editable-project input {
        border: none;
        outline: none;
        background: #ec8989;
        color: #fff;
        padding: 4px 6px;
        border-radius: 5px;
        margin: 7px 0;
    }

    .editable-project .back {
        cursor: pointer;
        margin: 5px 0;
        height: 33px;
        border-radius: .25rem;
        background: #fff;
        border: solid 2px #ec8989;
        color: #ec8989;
        font-size: 17px;
    }

    .editable-project .back:hover {
        color: #fff;
        background: #ec8989;
    }

    .editable-project .back span {
        margin: 0 -4px;
        line-height: 27px;
    }

    .editable-project button {
        margin: 5px 5px;
        background: #ec8989;
        color: #fff;
        font-size: 13px;
    }

    .editable-project button:hover {
        background: #d88080;
    }

    .remove-project {
        background: #ec8989;
        color: #fff;
        margin: 0 30px;
    }

    .remove-project:hover {
        background: #d88080;
    }

    .list-projects .project .card {
        border: none;
    }

    .list-projects .project .card .card-body .card-title {
        padding: 0;
    }

    .pagination div {
        color: #fff;
        font-size: 3rem;
        cursor: pointer;
    }

    .pagination div span:hover,
    .pagination div span:focus {
        color: #d88080;
    }

    .pagination .left-arrow span {
        position: absolute;
        left: 10px;
        top: 120px;
    }

    .pagination .right-arrow span {
        position: absolute;
        right: 10px;
        top: 120px;
    }

    .open-project {
        width: 8px;
        position: absolute;
        right: 12px;
        top: 0;
        height: 166px;
        background: #d88080;
    }

    .open-project:hover {
        cursor: pointer;
        width: 16px;
    }

    .open-project:hover::after {
        content: ">";
        color: #fff;
        width: 2px;
        height: 2px;
        font-size: 1.8rem;
        position: absolute;
        top: 38%;
        left: 1px;
    }

    .filters {
        margin: 10px 15px;
        height: 100%;
    }
</style>



