const get = (...keys) =>
  1 === keys.length
    ? localStorage.getItem(keys)
    : keys.map(key => localStorage.getItem(key))

const has = (...keys) => 1 === keys.length
  ? null !== get(keys)[0] : keys.map(key => null !== get(key)[0])

const set = (key, value) => {
  localStorage.setItem(key, value)

  return set
}

const remove = (...keys) => keys.map(key => localStorage.removeItem(key))

export default {
  get,
  has,
  set,
  remove,
}



