<template>
    <div>
        <spinner :enable="spinnerEnable"/>

        <div class="row col-md-11 header">
            <input type="text"
                   class="search col-md-9"
                   placeholder="Поиск"
                   v-model="search"
                   @keyup.enter="searchByName"
                   @dblclick="init">
            <div class="buttons col-md-3">
                <button class="btn col-md-5 create-task"
                        @click="creating = true">
                    Создать
                </button>
                <button class="btn col-md-5 filters"
                        @click="showFilters = !showFilters">
                    Фильтры
                </button>
            </div>
        </div>

        <transition name="bounce">
            <draggable class="row col-md-12 tasks"
                       v-if="!showFilters"
                       @end="updateSort"
                       v-model="tasks">
                <div style="width: 18rem;"
                     :class="['card task', closed === task.status ? 'closed' : 'opened']"
                     v-for="task in tasks">
                    <transition name="fade"
                                v-if="task.id !== removable.id">
                        <div class="card-body">
                            <h5 class="card-title">
                                <span :title="task.name">{{ task.name | taskLongName }}</span>
                                <span class="status">({{ closed === task.status ? 'Закрыта' : 'Открыта' }})</span>
                            </h5>
                            <p class="card-text">
                                Дедлайн: {{ null === task.deadline_at ? 'не задан' : task.deadline_at }}
                            </p>
                            <button class="btn"
                                    @click="editable = Object.assign({}, task)">
                                Редактировать
                            </button>
                            <button class="btn"
                                    @click="removable.id = task.id">Удалить
                            </button>
                        </div>
                    </transition>

                    <transition name="bounce"
                                v-else>
                        <button class="col-md-6 btn remove-project"
                                @click="remove">
                            Удалить?
                        </button>
                    </transition>
                </div>
            </draggable>
        </transition>

        <transition name="fade">
            <filters class="col-md-2 filters"
                     v-if="showFilters"
                     v-on:execute="executeFilter"/>
        </transition>

        <modal class="create-task_modal"
               v-if="creating">
            <template slot="header">
                Добавление задачи
            </template>

            <template slot="body">
                <input class="col-md-12"
                       type="text"
                       placeholder="Название"
                       v-model="newTask.name"
                       :title="'Название'">
                <input class="col-md-12"
                       type="text"
                       placeholder="Позиция"
                       v-model="newTask.sort"
                       :title="'Позиция'">
                <input class="col-md-12"
                       type="date"
                       placeholder="Дедлайн"
                       v-model="newTask.deadline_at"
                       :title="'Дедлайн'">
                <select class="col-md-12"
                        v-model="newTask.status"
                        :title="'Статус'">
                    <option :value="status.value"
                            v-text="status.name"
                            v-for="status in statuses">
                    </option>
                </select>
            </template>

            <template slot="footer">
                <button class="modal-default-button btn"
                        @click="store">
                    Создать
                </button>
                <button class="modal-default-button btn"
                        @click="creating = false">
                    Закрыть
                </button>
            </template>
        </modal>

        <modal class="edit-task_modal create-task_modal"
               v-if="undefined !== editable.id && 0 !== editable.id">
            <template slot="header">
                Редактирование {{ taskNameForEdit | taskLongName }}
            </template>

            <template slot="body">
                <input class="col-md-12"
                       type="text"
                       placeholder="Название"
                       v-model="editable.name"
                       :title="'Название'">
                <input class="col-md-12"
                       type="text"
                       placeholder="Позиция"
                       v-model="editable.sort"
                       :title="'Позиция'">
                <input class="col-md-12"
                       type="date"
                       placeholder="Дедлайн"
                       v-model="editable.deadline_at"
                       :title="'Дедлайн'">
                <select class="col-md-12"
                        v-model="editable.status"
                        :title="'Статус'">
                    <option :value="status.value"
                            v-text="status.name"
                            v-for="status in statuses">
                    </option>
                </select>
            </template>

            <template slot="footer">
                <button class="modal-default-button btn"
                        @click="update">
                    Обновить
                </button>
                <button class="modal-default-button btn"
                        @click="editable = {}">
                    Закрыть
                </button>
            </template>
        </modal>
    </div>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import draggable from 'vuedraggable'
  import notify from '../services/notify'
  import { fetchProject, fetchTasks, createTask, deleteTask, updateTasksSort, updateTask } from '../api/project'
  import { removeWhere, firstWhere } from '../utils/utils'
  import { taskStatuses } from '../config'
  import Spinner from './Spinner'
  import Modal from './Modal'
  import TaskFilters from './TaskFilters'

  const {required, minLength, between} = require('vuelidate/lib/validators')

  const setTitle = title => document.title = title
  const getTitle = () => document.title

  const opened = taskStatuses[0].value
  const closed = taskStatuses[1].value

  const newTaskDefault = {name: '', status: opened, sort: 0, deadline_at: null}

  export default {
    name: 'ProjectPage',
    mixins: [validationMixin],
    components: {
      Spinner, draggable, Modal,
      'filters': TaskFilters,
    },
    validations: {
      newTask: {
        name: {required, minLength: minLength(3)},
        status: {required, between: between(opened, closed)},
        sort: {required},
      },
      editable: {
        name: {required, minLength: minLength(3)},
        status: {required, between: between(opened, closed)},
        sort: {required},
      },
    },
    data: () => ({
      showFilters: false,
      creating: false,
      search: '',
      spinnerEnable: true,
      oldTasks: [],
      tasks: [],
      project: {},
      removable: {id: 0},
      editable: {},
      newTask: Object.assign({}, newTaskDefault),
      activeFilters: '&order_by_position=1&',
    }),
    watch: {
      tasks(_, oldTasks) {
        this.oldTasks = oldTasks
      },
    },
    computed: {
      statuses: () => taskStatuses,
      opened: () => opened,
      closed: () => closed,
      taskNameForEdit() {
        return firstWhere(this.tasks, t => t.id === this.editable.id).name
      },
    },
    async mounted() {
      await this.init()
    },
    methods: {
      async init() {
        this.search = ''
        this.editable = {}
        this.removable = {id: 0}
        this.newTask = newTaskDefault

        await fetchProject(this.$route.params.id)
          .then(({_, result}) => this.project = result.data.data)
          .catch(({_, result}) => {
            notify.warning({text: 'Произошла ошибка, попробуйте позже'})

            if (undefined === result.response) {
              return this.$router.push(`/project/${this.project.id}`)
            }
          })

        const {success, result} = await this.loadTasks()

        if (success) {
          this.tasks = result.data.data

          setTitle(`${this.project.name.slice(0, 5)}. Задач ${this.tasks.length}`)
        }

        this.spinnerEnable = false
      },
      async executeFilter(parameters) {
        this.spinnerEnable = true
        this.activeFilters = parameters

        const {success, result} = await this.loadTasks(`&search_by=${this.search}`)

        if (success) {
          this.tasks = result.data.data
        } else {
          notify.error({text: 'Произошла ошибка, попробуйте позже'})
        }

        this.spinnerEnable = false
        this.showFilters = false
      },
      async searchByName() {
        if (0 === this.search.length) {
          return
        }

        this.spinnerEnable = true

        const {success, result} = await this.loadTasks(`&search_by=${this.search}`)

        if (success) {
          this.tasks = result.data.data
        } else {
          notify.error({text: 'Произошла ошибка, попробуйте позже'})
        }

        this.spinnerEnable = false
      },
      store() {
        this.$v.$touch()

        if (this.$v.newTask.$invalid) {
          notify.warning({text: 'Упс, Вы не прошли валидацию :)'})

          return
        }

        createTask(this.project.id, this.newTask)
          .then(({success, result}) => {
            if (!success) {
              notify.warning({text: 'Упс, произошла ошибка'})

              return
            }

            this.newTask = Object.assign({}, newTaskDefault)
            this.tasks.push(result.data.data)
            this.creating = false

            setTitle(getTitle().replace(/Задач \d/, `Задач ${this.tasks.length}`))

            notify.success({text: 'Вы успешно добавили новую задачу'})
          })
      },
      update() {
        this.$v.$touch()

        if (this.$v.editable.$invalid) {
          notify.warning({text: 'Упс, Вы не прошли валидацию :)'})

          return
        }

        updateTask(this.project.id, this.editable.id, this.editable)
          .then(({success, _}) => {
            if (!success) {
              notify.warning({text: 'Упс, произошла ошибка'})

              return
            }

            this.init()

            notify.success({text: 'Вы успешно обновили задачу'})
          })
      },
      remove() {
        if (0 === this.removable.id) {
          return
        }

        deleteTask(this.project.id, this.removable.id)
          .then(({success, _}) => {
            if (!success) {
              notify.warning({text: 'Произошла ошибка, попробуйте позже'})

              return
            }

            notify.success({text: 'Вы успешно удалили задачу'})

            this.tasks = removeWhere(this.tasks, t => t.id === this.removable.id)

            this.removable = {id: 0}

            setTitle(getTitle().replace(/Задач \d/, `Задач ${this.tasks.length}`))
          })
      },
      updateSort() {
        updateTasksSort(
          this.project.id,
          this.tasks.map((task, index) => ({id: task.id, position: index})),
        )
          .then(({success, _}) => {
            if (!success) {
              notify.warning({text: 'Мы не смогли обновить позиции'})

              this.tasks = this.oldTasks

              return
            }

            notify.success({text: 'Мы успешно обновили позиции'})
          })
      },
      async loadTasks(getParams = null) {
        if (undefined === this.project.id) {
          return
        }

        return null === getParams
          ? await fetchTasks(this.project.id, this.activeFilters)
          : await fetchTasks(this.project.id, `${getParams}&${this.activeFilters}`)
      },
    },
  }
</script>

<style scoped>
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
    {
        opacity: 0;
    }

    .bounce-enter-active {
        animation: bounce-in 0.5s;
    }

    .bounce-leave-active {
        animation: bounce-in 0.5s reverse;
    }

    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }

    input::-webkit-input-placeholder {
        color: #fff;
    }

    .header {
        margin: 50px 60px;
        height: 50px;
        background: #ec8989;
        border-radius: 5px;
    }

    .header .search {
        color: #fff;
        margin: 5px 0;
        font-size: 18px;
        border: none;
        outline: none;
        background: none;
        height: 40px;
    }

    .header .buttons {
        margin: 5px 0px;
    }

    .header .buttons button {
        color: #fff;
        background: #d88080;
        margin: 0 10px;
    }

    .header .buttons button:hover {
        background: #ff9e9e;
        transition-delay: 0.2ms;
    }

    .tasks {
        margin-left: 40px;
    }

    .tasks .task {
        margin: 10px 20px;
        background: #fff;
        color: #6b6b6b;
        border: none;
        box-shadow: 0 0 1px rgba(0, 0, 0, .1);
    }

    .tasks .closed {
        box-shadow: 0 0 1px #ff0000;
    }

    .tasks .opened {
        box-shadow: 0 0 1px #00ff00;
    }

    .tasks .closed .card-body .card-title .status {
        color: #ff0000 !important;
        font-size: 13px;
    }

    .tasks .task .card-body button {
        font-size: 14px;
        background: none;
    }

    .tasks .closed .card-body button {
        border: solid 2px #ff0000;
        color: red;
    }

    .tasks .task .card-body button:hover {
        border: solid 2px transparent;
        color: #fff;
    }

    .tasks .closed .card-body button:hover {
        background: #b74545;
    }

    .tasks .opened .card-body button {
        border: solid 2px #00ff00;
    }

    .tasks .opened .card-body button:hover {
        background: #45b745;
    }

    .tasks .opened .card-body .card-title .status {
        color: #00ff00 !important;
        font-size: 13px;
    }

    .tasks .task .remove-project {
        background: #ec8989;
        color: #fff;
        margin: 40px 70px;
    }

    .create-task_modal .modal-body input:focus {
        border: solid 2px #ec8989;
        background: none;
        color: #ec8989;
    }

    .create-task_modal .modal-body input {
        margin: 5px 0;
        border: none;
        background: #ec8989;
        height: 35px;
        border-radius: 2px;
        color: #fff;
        outline: none;
        padding-left: 4px;
    }

    .create-task_modal .modal-body select {
        margin: 10px 0;
        background: #ec8989;
        color: #fff;
        border: none;
        height: 35px;
        border-radius: 2px;
    }
</style>




