import { profile as me } from '../api/user'

import storage from './storage'

const profile = (store, jwt) => new Promise((resolve, reject) => {
  if (!jwt) {
    reject('Has no jwt')
  }

  me()
    .then(response => {
      store.dispatch('SET_USER', response.data.data)

      resolve()
    })
    .catch(err => reject(err))
})

export default {
  profile,
}


