export default {
  user(state) {
    return state.user
  },

  userIsGuest(state) {
    return undefined === state.user || 0 === Object.keys(state.user).length
  },

  projects(state) {
    return state.projects
  },

  tasks(state) {
    return state.tasks
  }
}



