export default {
  SET_USER: (state, user) => state.user = user,

  LOGOUT: (state) => state.user = {},

  SET_PROJECTS: (state, projects) => state.projects = projects,

  ADD_PROJECT: (state, project) => {
    if (8 === state.projects.length) {
      state.projects = [project, ...state.projects.slice(0, 7)]

      return
    }

    state.projects.push(project)
  },

  SET_TASKS: (state, tasks) => state.tasks = tasks,

  ADD_TASK: (state, task) => {
    if (12 === state.tasks.length) {
      state.tasks = [task, ...state.tasks.slice(0, 11)]

      return
    }

    state.tasks.push(task)
  },
}



