<template>
    <div>
        <div class="form-signin">
            <h1 class="h3 mb-3 font-weight-normal">Регистрация</h1>
            <label for="inputEmail" class="sr-only">Ваш email</label>
            <input type="email"
                   id="inputEmail" class="form-control"
                   placeholder="Ваш email"
                   name="email"
                   autofocus
                   v-model="email">
            <label for="inputPassword" class="sr-only">Ваш пароль</label>
            <input type="password"
                   id="inputPassword"
                   class="form-control"
                   placeholder="Ваш пароль"
                   name="password"
                   v-model="password">
            <button class="btn btn-lg btn-primary btn-block"
                    @click="register">
                Вперед
            </button>
        </div>
    </div>
</template>

<script>
  import { validationMixin } from 'vuelidate'
  import { register as registration } from '../api/user'
  import notify from '../services/notify'

  const {required, email} = require('vuelidate/lib/validators')

  export default {
    name: 'Registration',
    mixins: [validationMixin],
    validations: {
      email: {
        required,
        email,
      },
      password: {
        required,
      },
    },
    data: () => ({
      email: '',
      password: '',
    }),
    methods: {
      register () {
        this.$v.$touch()

        if (this.$v.$invalid) {
          if (!this.$v.email.required) {
            notify.warning({text: 'Email обязателен к заполнению'})
          }

          if (!this.$v.email.email) {
            notify.warning({text: 'Email не корректен'})
          }

          if (!this.$v.password.required) {
            notify.warning({text: 'Пароль обязателен к заполнению'})
          }

          return
        }

        registration({email: this.email, password: this.password})
          .then(_ => {
            notify.success({text: 'Вы успешно зарегистрировались'})

            this.$router.push('/')
          })
          .catch(err => {
            for (let k in err.response.data) {
              notify.warning({text: err.response.data[k].join(', ')})
            }
          })
      },
    },
  }
</script>

<style scoped>
    .form-signin {
        width: 100%;
        max-width: 330px;
        padding: 15px;
        margin: 50px auto;
        background: #fff;
    }

    .form-signin .checkbox {
        font-weight: 400;
    }

    .form-signin .form-control {
        position: relative;
        box-sizing: border-box;
        height: auto;
        padding: 10px;
        font-size: 16px;
    }

    .form-signin .form-control:focus {
        z-index: 2;
    }

    .form-signin input[type='email'] {
        margin-bottom: -1px;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
    }

    .form-signin input[type='password'] {
        margin-bottom: 10px;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
</style>



