import storage from '../services/storage'

export default {
  SET_USER: ({commit}, user) => commit('SET_USER', user),

  LOGOUT: ({commit}) => {
    commit('LOGOUT')

    storage.remove('jwt', 'user')
  },

  SET_PROJECTS: ({commit}, projects) => commit('SET_PROJECTS', projects),

  ADD_PROJECT: ({commit}, project) => commit('ADD_PROJECT', project),

  SET_TASKS: ({commit}, tasks) => commit('SET_TASKS', tasks),

  ADD_TASK: ({commit}, task) => commit('ADD_TASK', task),
}



