<template>
    <div>
        <div class="col-md-12 filter order">
            <h5>Фильтрация</h5>
            <select class="custom-select"
                    @change="executeOrderBy"
                    v-model="selectedOrder">
                <option v-for="order in orders"
                        :value="order.value"
                        v-text="order.name"
                        :selected="order.selected">
                </option>
            </select>
        </div>
    </div>
</template>

<script>
  import { firstWhere } from '../utils/utils'

  export default {
    name: 'ProjectFilters',
    data: () => ({
      selectedOrder: 'position',
      orderBy: 'desc',
    }),
    computed: {
      orders: () => [
        {value: 'position', name: 'Позиции', callback: () => '&order_by_position=1&'},
        {value: 'name', name: 'Названию', callback: () => '&order_by_name=1&'},
        {value: 'tasks', name: 'Задачам', callback: () => '&order_by_tasks=1&'},
        {value: 'completed_tasks', name: 'Выполненым задачам', callback: () => '&order_by_completed_tasks=1&'},
        {value: 'incompleted_tasks', name: 'Не выполненым задачам', callback: () => '&order_by_incompleted_tasks=1&'},
      ],
    },
    methods: {
      executeOrderBy () {
        const getParams = firstWhere(this.orders, filter => this.selectedOrder === filter.value)
          .callback() + `order_by=${this.orderBy}`

        this.$emit('execute', getParams)
      },
    },
  }
</script>

<style scoped>
    .filter {
        margin: 5px 10px;
        height: 100px;
    }

    .filter h5 {
        color: #3f3f3f;
        font-size: 17px;
        text-align: center;
        padding: 10px 0;
    }

    .filter select {
        border: none;
        color: #ccc;
    }
</style>


