<template>
    <div id="app">
        <div>
            <nav class="navbar navbar-light justify-content-between">
                <a class="navbar-brand">{{ welcome }}</a>
                <div class="buttons">
                    <div v-if="userIsGuest">
                        <router-link to="/register">
                            <button type="button" class="btn register">Регистрация</button>
                        </router-link>
                        <router-link to="/authorization">
                            <button type="button" class="btn authorization">Авторизация</button>
                        </router-link>
                    </div>
                    <div v-else>
                        <router-link to="/faq">
                            <button type="button" class="btn authorization">Гайд</button>
                        </router-link>
                        <button type="button"
                                class="btn register"
                                @click="logout">Выход
                        </button>
                    </div>
                </div>
            </nav>

            <transition name="fade" mode="out-in">
                <router-view class="view"/>
            </transition>
        </div>

        <notifications group="all" position="bottom right"/>
    </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import notify from './services/notify'
  import storage from './services/storage'
  import user from './services/user'

  export default {
    name: 'App',
    async mounted() {
      const jwt = storage.get('jwt')

      if (jwt) {
        user.profile(this.$store, jwt)
            .catch(err => {
              storage.remove('jwt')

              if (401 === err.response.status) {
                notify.warning({text: 'Вы должны авторизоваться'})

                return
              }

              notify.error({text: 'Возникла ошибка, попробуйте позже'})
            })
      }
    },
    computed: {
      ...mapGetters(['userIsGuest']),
      welcome() {
        return this.userIsGuest
          ? 'Авторизуйтесь для совершения действий'
          : `Добро пожаловать, ${this.$store.getters.user.email}`
      },
    },
    methods: {
      ...mapActions({
        logout: 'LOGOUT',
      }),
    },
  }
</script>

<style>
    @font-face {
        font-family: 'Acrom';
        src: url('../static/fonts/Acrom/Acrom-Regular.eot');
        src: url('../static/fonts/Acrom/Acrom-Regular.eot?#iefix') format('embedded-opentype'),
        url('../static/fonts/Acrom/Acrom-Regular.woff') format('woff'),
        url('../static/fonts/Acrom/Acrom-Regular.ttf') format('truetype');
    }

    body {
        margin: 0 250px;
        font-family: 'Acrom';
        background: #f2fffe;
    }

    .navbar {
        height: 60px;
        background: #fff;
    }

    .navbar .buttons button {
        color: #fff;
        border: none;
        border-radius: 5px;
    }

    .navbar .buttons .register {
        background: #ec8989;
        border: solid 2px #ec8989;
    }

    .navbar .buttons .register:hover {
        background: #fff;
        color: #ec8989;
    }

    .navbar .buttons .authorization {
        background: #fff;
        color: #ec8989;
        border: solid 2px #ec8989;
    }

    .navbar .buttons .authorization:hover {
        background: #ec8989;
        color: #fff;
    }

    .fade-enter-active,
    .fade-leave-active {
        transition: all 0.2s ease;
    }

    .fade-enter,
    .fade-leave-active {
        opacity: 0;
    }
</style>



