import axios from 'axios'
import {
  buildUrlJwt,
} from '../config'
import { destResponse } from '../utils/main'

export const fetchTasks = parameters => new Promise(resolve =>
  axios
    .get(buildUrlJwt('tasks', parameters))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const createTask = parameters => new Promise(resolve =>
  axios
    .post(buildUrlJwt('tasks'), parameters)
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const updateTask = (id, parameters) => new Promise(resolve =>
  axios
    .put(buildUrlJwt(`tasks/${id}`), parameters)
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const deleteTask = id => new Promise(resolve =>
  axios
    .delete(buildUrlJwt(`tasks/${id}`))
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)

export const updateTasksSort = parameters => new Promise(resolve =>
  axios
    .put(buildUrlJwt('sort/tasks'), parameters)
    .then(res => resolve(destResponse(res)))
    .catch(err => resolve(destResponse(err, false))),
)



