import storage from '@/services/storage'

export const serverUrl = 'https://rubygarage.tk/'

export const buildUrl = (url, params = null) => null === params ? `${serverUrl}${url}` : `${serverUrl}${url}${params}`

export const buildUrlJwt = (url, params = null) => {
  return null === params ?
    `${buildUrl(url)}?token=${storage.get('jwt')}` : `${buildUrl(url)}?token=${storage.get('jwt')}${params}`
}

export const taskStatuses = [{value: 0, name: 'Открыто'}, {value: 1, name: 'Закрыто'}]


