import Vue from 'vue'
import Notifications from 'vue-notification'

Vue.use(Notifications)

const vue = new Vue()

const notification = parameters => vue.$notify(parameters)

const success = ({title = 'Успех', text} = {}) =>
  notification({group: 'all', type: 'success', title: title, text: text})

const warning = ({title = 'Предупреждение', text} = {}) =>
  notification({group: 'all', type: 'warn', title: title, text: text})

const error = ({title = 'Ошибка', text} = {}) =>
  notification({group: 'all', type: 'error', title: title, text: text})

const info = ({title = 'Информация', text} = {}) =>
  notification({group: 'all', type: 'info', title: title, text: text})

export default {
  success,
  warning,
  error,
  info,
}



