<template>
    <div v-if="!userIsGuest">
        <projects/>
        <tasks/>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Projects from './Projects'
  import Tasks from './Tasks'

  export default {
    name: 'IndexPage',
    components: {
      Projects, Tasks
    },
    computed: {
      ...mapGetters(['userIsGuest']),
    },
  }
</script>

<style scoped>
</style>



