<template>
    <div>
        <spinner :enable="spinnerEnable"/>

        <div class="row col-md-12 tasks" v-if="!spinnerEnable">
            <div class="row col-md-10 all-tasks">
                <div class="col-md-12 header-tasks">
                    <div v-if="!isCreating">
                        <input type="text"
                               class="col-md-10 search-input"
                               placeholder="Поиск..."
                               v-model="search"
                               @keyup.enter="searchByName"
                               @dblclick="init">
                        <button class="btn add-task"
                                @click="isCreating = true">
                            Добавить
                        </button>
                    </div>
                    <div v-else>
                        <input type="text"
                               class="col-md-12 input-new_task-name"
                               placeholder="Название..."
                               v-model="newTask.name"
                               @keyup.enter="store">
                    </div>
                </div>
                <div class="col-md-12 pagination">
                    <div class="left-arrow"
                         v-if="8 <= skip"
                         @click="paginationPrev">
                        <span>&#60;</span>
                    </div>
                    <div class="right-arrow"
                         v-if="0 !== tasks.length"
                         @click="paginationNext">
                        <span>&#62;</span>
                    </div>
                </div>
                <div class="row col-md-11 list-tasks">
                    <transition name="bounce">
                        <div class="row col-md-12"
                             v-if="!fadeAnimation">
                            <div class="col-sm-3 task"
                                 v-for="task in tasks"
                                 :key="task.id">
                                <div :class="['card', opened === parseInt(task.status) ? 'task-opened' : 'task-closed']">
                                    <div class="card-body">
                                        <div v-if="task.id !== removable.id">
                                            <h5 class="card-title"
                                                @dblclick="Object.assign(editable, task)"
                                                :title="task.name">
                                                {{ task.name | taskLongName }}
                                            </h5>
                                            <div @dblclick="removable = task"
                                                 ref="removeEvent">
                                                <p class="card-text">Дедлайн: {{ task.deadline_at || 'не задан' }}</p>
                                            </div>
                                        </div>
                                        <transition name="fade">
                                            <div v-if="removable.id === task.id"
                                                 @dblclick="removable = {id: 0}">
                                                <button class="btn remove-task"
                                                        @click="remove">
                                                    Удалить?
                                                </button>
                                            </div>
                                        </transition>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </transition>
                </div>
            </div>
            <filters class="col-md-2 filters"
                     v-on:execute="executeFilter"/>
        </div>

        <modal v-if="0 !== editable.id">
            <template slot="header">Обновление</template>

            <template slot="body">
                <div class="editable-tasks_modal_body">
                    <input type="text"
                           class="col-md-12"
                           v-model="editable.name"
                           :title="'Название'">
                    <input type="text"
                           class="col-md-12"
                           v-model="editable.sort"
                           :title="'Порядок'">
                    <input type="date"
                           class="col-md-12"
                           v-model="editable.deadline_at"
                           :title="'Дедлайн'">
                    <select v-model="editable.status">
                        <option :value="opened">Открыта</option>
                        <option :value="closed">Закрыта</option>
                    </select>
                </div>
            </template>

            <template slot="footer">
                <button class="modal-default-button btn"
                        @click="update">
                    Обновить
                </button>
                <button class="modal-default-button btn"
                        @click="editable = {id: 0}">
                    Закрыть
                </button>
            </template>
        </modal>
    </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import {
    fetchTasks,
    createTask,
    updateTask,
    deleteTask,
  } from '../api/task'
  import notify from '../services/notify'
  import Spinner from './Spinner'
  import TaskFilters from './TaskFilters'
  import Modal from './Modal'

  const take = 12
  const opened = 0
  const closed = 1

  export default {
    name: 'Tasks',
    components: {
      Spinner,
      filters: TaskFilters,
      modal: Modal
    },
    data: () => ({
      skip: 0,
      spinnerEnable: false,
      isCreating: false,
      search: '',
      newTask: {name: ''},
      editable: {id: 0, name: '', sort: 0},
      removable: {id: 0},
      fadeAnimation: false,
      activeFilters: '&order_by_position=1&',
    }),
    async mounted () {
      await this.init()
    },
    computed: {
      ...mapGetters(['tasks', 'userIsGuest']),
      myTasks: {
        get () {
          return this.tasks
        },
        set (tasks) {
          this.setTasks(tasks)
        },
      },
      closed: () => closed,
      opened: () => opened
    },
    methods: {
      ...mapActions({
        addTask: 'ADD_TASK',
        setTasks: 'SET_TASKS',
        logout: 'LOGOUT',
      }),
      async init () {
        if (!this.userIsGuest) {
          this.search = ''
          this.spinnerEnable = true
          this.skip = 0
          this.activeFilters = '&order_by_position=1&'

          const {success, result} = await this.loadTasks()

          if (!success) {
            if (undefined === result.response) {
              notify.error({text: 'Произошла ошибка, попробуйте позже'})
            }

            if (401 === result.response.status) {
              notify.warning({text: 'Вы должны авторизоваться'})

              this.logout()

              return this.$router.push('/')
            }
          }

          await this.setTasks(result)

          this.spinnerEnable = false
        }
      },
      async executeFilter (parameters) {
        this.fadeAnimation = true
        this.skip = 0
        this.activeFilters = parameters

        const {success, result} = await this.loadTasks(`&search_by=${this.search}`)

        if (success) {
          await this.setTasks(result)
        } else {
          notify.error({text: 'Произошла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
      async searchByName () {
        if (0 === this.search.length) {
          return
        }

        this.fadeAnimation = true
        this.skip = 0

        const {success, result} = await this.loadTasks(`&search_by=${this.search}`)

        if (success) {
          await this.setTasks(result)
        } else {
          notify.error({text: 'Произошла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
      store () {
        if (2 >= this.newTask.name.length) {
          notify.warning({text: 'Минимальное название должно состоять из 3х символов'})

          return
        }

        createTask({name: this.newTask.name, status: this.opened})
          .then(({success, result}) => {
            if (success) {
              this.addTask(result.data.data)

              this.newTask.name = ''

              notify.success({text: 'Вы успешно добавили новую задачу'})
            } else {
              notify.error({text: 'Произошла ошибка, попробуйте позже'})
            }
          })

        this.isCreating = false
      },
      update () {
        if (0 === this.editable.name.length) {
          notify.warning({text: 'Вы забыли ввести название'})

          return
        }

        updateTask(this.editable.id, {
          name: this.editable.name,
          sort: this.editable.sort,
          status: this.editable.status,
          deadline_at: this.editable.deadline_at
        })
          .then(({success, _}) => {
            if (success) {
              this.editable = {id: 0, name: '', sort: 0}

              this.init()

              notify.success({text: 'Вы успешно обновили задачу'})
            } else {
              notify.error({text: 'Произошла ошибка, попробуйте позже'})
            }
          })
      },
      remove () {
        deleteTask(this.removable.id)
          .then(({success, _}) => {
            if (success) {
              this.removable = {id: 0}

              notify.success({text: 'Вы успешно удалили задачу'})

              this.init()
            } else {
              notify.error({text: 'Произошла ошибка, попробуйте позже'})
            }
          })
      },
      async loadTasks (getParams = null) {
        const {success, result} =
          null === getParams
            ? await fetchTasks(`${this.activeFilters}&skip=${this.skip}&take=${take}`)
            : await fetchTasks(`${getParams}&${this.activeFilters}&skip=${this.skip}&take=${take}`)

        if (success) {
          return {success, result: result.data.data}
        }

        return {success, result}
      },
      async paginationPrev () {
        if (0 === this.skip) {
          return
        }

        this.fadeAnimation = true
        this.skip -= take

        const {success, result} = await this.loadTasks(`&search_by=${this.search}`)

        if (success) {
          if (0 !== result.length) {
            this.setTasks(result)
          } else {
            this.skip += take

            notify.info({text: 'Задач нету :('})
          }
        } else {
          notify.error({text: 'Возникла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
      async paginationNext () {
        this.fadeAnimation = true
        this.skip += take

        const {success, result} = await this.loadTasks(`&search_by=${this.search}`)

        if (success) {
          if (0 !== result.length) {
            this.setTasks(result)
          } else {
            this.skip -= take

            notify.info({text: 'Задач нету :('})
          }
        } else {
          notify.error({text: 'Возникла ошибка, попробуйте позже'})
        }

        this.fadeAnimation = false
      },
    },
  }
</script>

<style scoped>
    .fade-enter-active,
    .fade-leave-active {
        transition: opacity 0.5s;
    }

    .fade-enter, .fade-leave-to /* .fade-leave-active до версии 2.1.8 */
    {
        opacity: 0;
    }

    .bounce-enter-active {
        animation: bounce-in 0.5s;
    }

    .bounce-leave-active {
        animation: bounce-in 0.5s reverse;
    }

    @keyframes bounce-in {
        0% {
            transform: scale(0);
        }
        50% {
            transform: scale(1.5);
        }
        100% {
            transform: scale(1);
        }
    }

    input::-webkit-input-placeholder {
        color: #fff;
    }

    .tasks {
        background: #fff;
        margin: 60px 0;
        height: 450px;
    }

    .all-tasks {
        height: 100%;
        background: #ec8989;
    }

    .header-tasks {
        height: 50px;
        border-bottom: solid 2px #fff;
        margin: 10px auto;
    }

    .header-tasks input {
        height: 40px;
        margin-left: 10px;
        outline: none;
        border: none;
        background: none;
        color: #fff;
    }

    .header-tasks button {
        margin-left: 55px;
        background: none;
        border: solid 1px #fff;
        color: #fff;
    }

    .header-tasks button:hover {
        background: #d88080;
        border: solid 1px #d88080;
    }

    .list-tasks {
        margin: 0 55px;
    }

    .list-tasks .task {
        margin: 10px 0 50px 0;
        height: 60px;
        font-size: 13px;
        z-index: 1;
    }

    .list-tasks .task-opened {
        box-shadow: 1px 2px 3px #3fff00;
    }

    .list-tasks .task-closed {
        box-shadow: 1px 2px 3px #ff0000;
    }

    .remove-task {
        background: #ec8989;
        color: #fff;
        margin: 0 30px;
    }

    .remove-task:hover {
        background: #d88080;
    }

    .list-tasks .task .card {
        border: none;
    }

    .list-tasks .task .card .card-body .card-title {
        padding: 0;
    }

    .pagination div {
        color: #fff;
        font-size: 3rem;
        cursor: pointer;
    }

    .pagination div span:hover,
    .pagination div span:focus {
        color: #d88080;
    }

    .pagination .left-arrow span {
        position: absolute;
        left: 10px;
        top: 120px;
    }

    .pagination .right-arrow span {
        position: absolute;
        right: 10px;
        top: 120px;
    }

    .filters {
        margin: 10px 15px;
        height: 100%;
    }

    .editable-tasks_modal_body input {
        margin: 10px 0;
        border: none;
        background: #d88080;
        border-radius: 3px;
        color: #fff;
        outline: none;
        padding: 7px 10px;
    }

    .editable-tasks_modal_body select {
        width: 100%;
        margin: 10px 0;
        border: none;
        background: #d88080;
        border-radius: 3px;
        color: #fff;
        outline: none;
        padding: 7px 10px;
    }
</style>



