import axios from 'axios'
import {
  buildUrl,
  buildUrlJwt,
} from '../config'

export const register = parameters => new Promise((resolve, reject) => {
  axios
    .post(buildUrl('register'), parameters)
    .then(res => resolve(res))
    .catch(err => reject(err))
})

export const authorization = parameters => new Promise((resolve, reject) => {
  axios
    .post(buildUrl('authorization'), parameters)
    .then(res => resolve(res))
    .catch(err => reject(err))
})

export const profile = () => new Promise((resolve, reject) => {
  axios
    .get(buildUrlJwt('me'))
    .then(res => resolve(res))
    .catch(err => reject(err))
})



