<template>
    <div>
        <div class="col-md-12 filter order">
            <h5>Фильтрация</h5>
            <select class="custom-select"
                    @change="execute"
                    v-model="selectedOrder">
                <option v-for="order in orders"
                        :value="order.value"
                        v-text="order.name"
                        :selected="order.selected"></option>
            </select>
        </div>

        <div class="col-md-12 filter duplicated">
            <h5>Повторения</h5>

            <input class="col-md-12"
                   type="text"
                   placeholder="Название"
                   v-model="duplicated.name">
            <select class="col-md-12"
                    v-model="duplicated.status">
                <option :value="status.value"
                        v-for="status in statuses"
                        v-text="status.name">
                </option>
            </select>

            <button class="col-md-12 btn"
                    @click="execute">
                Ок
            </button>
        </div>
    </div>
</template>

<script>
  import { firstWhere } from '../utils/utils'
  import { taskStatuses } from '../config'

  export default {
    name: 'TaskFilters',
    data: () => ({
      selectedOrder: 'position',
      orderBy: 'desc',
      duplicated: {name: '', status: 0}
    }),
    computed: {
      orders: () => [
        {value: 'position', name: 'Позиции', callback: () => '&order_by_position=1&'},
        {value: 'opened', name: 'Открытым задачам', callback: () => '&order_by_opened=1&'},
        {value: 'closed', name: 'Закрытым задачам', callback: () => '&order_by_closed=1&'},
      ],
      statuses: () => taskStatuses
    },
    methods: {
      execute () {
        const params = `${this.filterOrderBy()}${this.duplicatedSearch()}`

        this.$emit('execute', params)
      },
      filterOrderBy () {
        return firstWhere(this.orders, filter => this.selectedOrder === filter.value)
          .callback() + `order_by=${this.orderBy}`
      },
      duplicatedSearch () {
        if (0 === this.duplicated.name.length) {
          return
        }

        return `&duplicated=1&duplicated_status=${this.duplicated.status}&duplicated_name=${this.duplicated.name}&`
      }
    },
  }
</script>

<style scoped>
    .filter {
        margin: 5px 10px;
        height: 100px;
    }

    .filter h5 {
        color: #3f3f3f;
        font-size: 17px;
        text-align: center;
        padding: 10px 0;
    }

    .filter select {
        background: #d88080;
        border: none;
        color: #fff;
    }

    .duplicated input:hover::-webkit-input-placeholder {
        color: #fff;
    }

    .duplicated input::-webkit-input-placeholder {
        color: #d88080;
    }

    .duplicated input {
        outline: none;
        margin: 5px 0;
        padding: 4px 7px;
        border: solid 2px #d88080;
        color: #d88080;
        border-radius: 3px;
    }

    .duplicated input:hover {
        background: #d88080;
        color: #fff;
    }

    .duplicated select {
        background: #d88080;
        color: #fff;
        margin: 5px 0;
        padding: 6px 2px;
        border-radius: 3px;
    }

    .duplicated button {
        margin: 3px 0;
        background: none;
        border: solid 2px #d88080;
        color: #d88080;
    }

    .duplicated button:hover {
        background: #d88080;
        color: #fff;
    }
</style>


